import { defineDocumentType, makeSource } from 'contentlayer/source-files';

// Define the Blog document type
export const Blog = defineDocumentType(() => ({
  name: 'Blog',
  filePathPattern: `blog/*.mdx`,
  contentType: 'mdx',
  fields: {
    title: { type: 'string', required: true },
    slug: { type: 'string', required: true },
    tags: { type: 'list', of: { type: 'string' }, required: false },
    readingTime: { type: 'string', required: false },
    seo: {
      type: 'nested',
      of: {
        title: { type: 'string', required: true },
        description: { type: 'string', required: true },
      },
      required: true,
    },
    schema: {
      type: 'nested',
      of: {
        type: { type: 'string', required: true },
        provider: { type: 'string', required: true },
      },
      required: true,
    },
  },
  computedFields: {
    url: {
      type: 'string',
      resolve: (doc) => `/blog/${doc.slug}`,
    },
  },
}));

// Define the Course document type
export const Course = defineDocumentType(() => ({
  name: 'Course',
  filePathPattern: `courses/*.mdx`,
  contentType: 'mdx',
  fields: {
    title: { type: 'string', required: true },
    slug: { type: 'string', required: true },
    level: { type: 'string', required: true },
    duration: { type: 'string', required: true },
    priceINR: { type: 'number', required: true },
    priceAED: { type: 'number', required: true },
    audience: { type: 'list', of: { type: 'string' }, required: true },
    tags: { type: 'list', of: { type: 'string' }, required: false },
    short: { type: 'string', required: true },
    seo: {
      type: 'nested',
      of: {
        title: { type: 'string', required: true },
        description: { type: 'string', required: true },
      },
      required: true,
    },
    schema: {
      type: 'nested',
      of: {
        type: { type: 'string', required: true },
        provider: { type: 'string', required: true },
      },
      required: true,
    },
  },
  computedFields: {
    url: {
      type: 'string',
      resolve: (doc) => `/courses/${doc.slug}`,
    },
  },
}));

export default makeSource({
  contentDirPath: 'content',
  documentTypes: [Blog, Course],
});
